import { Component, OnInit } from '@angular/core';
import { RimsService } from "../rims.service"
import { ActivatedRoute, Router } from "@angular/router";
import { RimModel } from "../models/rim-model";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: 'app-edit-rim',
  templateUrl: './edit-rim.component.html',
  styleUrls: ['./edit-rim.component.css']
})
export class EditRimComponent implements OnInit {
  editModel: RimModel;
  constructor(private route : ActivatedRoute,
              private router : Router,
              private rimsService : RimsService,
              private toastr: ToastrService) { }

  ngOnInit() {
    this.rimsService.getRimById(
      this.route.snapshot.params['id']
    ).subscribe((data) => {
      this.editModel = data;
    })
  }

  edit() {
    this.rimsService
      .editRim(this.editModel._id, this.editModel)
      .subscribe(() => {
        this.toastr.success('Edited Rim successfully!', 'Success!');
        this.router.navigateByUrl('/rims/catalog');
      }, (err) => {
        this.toastr.error('Could not edit rim ad!', 'Error!');
      });
  }

}
