import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

//Constants
const kinveyBaseUrl = "https://baas.kinvey.com/";
const kinveyAppKey = "kid_SJEtf6PHm";

//Requests URLs.
const createOrderUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/orders';
const archiveOrderUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/ordersArchive';
const allOrdersUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/orders';
const deleteOrderUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/orders/';

@Injectable()
export class OrdersService {
  constructor(private http: HttpClient) { }
  //Create
  submitOrder(body: any) {
    return this.http.post(createOrderUrl, body);
  }
  archiveOrder(body: any) {
    return this.http.post(archiveOrderUrl, body);
  }

  //Retrieve
  getAllOrders() {
    return this.http.get(allOrdersUrl);
  }

  //Delete
  deleteOrder(id: string) {
    return this.http.delete(deleteOrderUrl + id);
  }
}
