import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AppRoutingModule } from './app.routing';
import { ToastrModule } from "ngx-toastr";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { AuthModule } from "./authentication/auth.module";
import { RimsModule } from "./rims/rims.module";

import { AppComponent } from './app.component';
import { NavigationComponent } from './navigation/navigation.component';
import { HomeComponent } from './home/home.component';
import { KinveyInterceptor } from "./interceptors/kinvey.interceptor"
import { ErrorInterceptor} from "./interceptors/error.interceptor";
import { CustomFormsModule } from "ng2-validation";
import { CartDetailsComponent } from './cart/cart-details/cart-details/cart-details.component';
import { OrdersService } from "./orders/orders.service";
import { AllOrdersComponent } from './orders/all-orders/all-orders.component';
import { NotFoundComponent } from "./not-found/not-found.component";
import { ContactsComponent } from './contacts/contacts.component';
import { FooterComponent } from './footer/footer.component';

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    HomeComponent,
    CartDetailsComponent,
    AllOrdersComponent,
    NotFoundComponent,
    ContactsComponent,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    CustomFormsModule,
    AppRoutingModule,
    HttpClientModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
    AuthModule,
    RimsModule
  ],
  providers: [
    OrdersService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: KinveyInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    }

  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
