import { Component, OnInit } from '@angular/core';
import { AuthService } from "../authentication/auth.service";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  currentImage: string = '../../assets/slideshow/slide1.jpg';
  username: string;

  constructor(private authService: AuthService) {
    if (localStorage.getItem('user')) {
      this.username = JSON.parse(localStorage.getItem('user')).username;
    }
  }

  ngOnInit() {
    setTimeout(() => {
      if (this.currentImage === '../../assets/slideshow/slide1.jpg') {
        this.currentImage = '../../assets/slideshow/slide2.jpg';
      } else if (this.currentImage === '../../assets/slideshow/slide2.jpg') {
        this.currentImage = '../../assets/slideshow/slide3.jpg';
      } else if (this.currentImage === '../../assets/slideshow/slide3.jpg') {
        this.currentImage = '../../assets/slideshow/slide1.jpg';
      }
      this.ngOnInit();
    }, 3000);

  }
}
