import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// Components
import { HomeComponent } from './home/home.component';
import { SigninComponent } from './authentication/signin/signin.component';
import { SignupComponent } from './authentication/signup/signup.component';
import { AccountComponent } from "./authentication/account/account.component";
import { CartDetailsComponent } from "./cart/cart-details/cart-details/cart-details.component"
import { RimsModule } from "./rims/rims.module";
import { AllOrdersComponent } from "./orders/all-orders/all-orders.component";
import { ContactsComponent } from "./contacts/contacts.component";
import { NotFoundComponent } from "./not-found/not-found.component";
import { UserInfoComponent } from "./authentication/user-info/user-info.component";

//Guards
import { AuthGuard } from "./authentication/guards/auth.guard";
import { AdminGuard } from "./authentication/guards/admin.guard";


const routes : Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'home' },
  { path: 'home', component: HomeComponent },
  { path: 'login', component: SigninComponent },
  { path: 'register', component: SignupComponent },
  { path: 'user/:id', component: UserInfoComponent },
  { path: 'account', component: AccountComponent, canActivate: [ AuthGuard ]  },
  { path: 'cart', component: CartDetailsComponent, canActivate: [ AuthGuard ]  },
  { path: 'rims', loadChildren: () => RimsModule, canActivate: [ AuthGuard ] },
  { path: 'orders/all', component: AllOrdersComponent , canActivate: [ AuthGuard, AdminGuard ]  },
  { path: 'contacts', component: ContactsComponent },
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {  }
