import { Component, OnInit } from '@angular/core';
import { OrdersService } from "../orders.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: 'app-all-orders',
  templateUrl: './all-orders.component.html',
  styleUrls: ['./all-orders.component.css']
})
export class AllOrdersComponent implements OnInit {
  orders: any;
  constructor(private ordersService: OrdersService,
              private toastr: ToastrService) { }

  ngOnInit() {
    this.ordersService.getAllOrders()
      .subscribe((data) => {
        this.orders = data;
        if (this.orders.length === 0) {
          this.orders = undefined;
        }
      })
  }

  archiveOrder(id: string) {
    let body = this.orders.filter((order) => order._id === id)[0];
    this.ordersService.archiveOrder(body)
      .subscribe((data) => {
        this.ordersService.deleteOrder(body._id)
          .subscribe((res) => {
            this.toastr.success('Archived order!', 'Success!');
            this.ngOnInit();
          }, (err) => {
            this.toastr.error('Could not archive order!', 'Error!');
          })
      }, (err) => {
        this.toastr.error('Could not archive order!', 'Error!');
      })
  }

  deleteOrder(id: string) {
    this.ordersService.deleteOrder(id)
      .subscribe(() => {
        this.ordersService.getAllOrders()
          .subscribe((data) => {
            this.orders = data;
            this.toastr.success('Order deleted successfully!', 'Success!');
          }, (err) => {
            this.toastr.error('Something went wrong!', 'Error!');
          })
      });
  }

}
