import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Observable } from "rxjs/index";

import { RimModel } from "../models/rim-model";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import { RimsService } from "../rims.service"

@Component({
  selector: 'app-details-rim',
  templateUrl: './details-rim.component.html',
  styleUrls: ['./details-rim.component.css']
})
export class DetailsRimComponent implements OnInit {
  rimModel: Observable<RimModel>;
  rimsCount: number = 4;
  id: string;

  product: RimModel;

  constructor(private route: ActivatedRoute,
              private rimsService: RimsService,
              private toastr: ToastrService,
              private router: Router)
  {
    this.id = this.route.snapshot.params['id'];
  }

  ngOnInit() {
    this.rimModel = this.rimsService.getRimById(this.id);
    this.rimModel.subscribe((data) => {
      this.product = data;
    });
  }

  addToCart() {
    this.product['count'] = this.rimsCount;
    this.product['totalPrice'] = this.product.price * this.rimsCount;


    //We will set the cart in localStorage, so on logout - cart will discard its products.
    let cart = [];
    let duplicateProduct = false;
    if (!localStorage.getItem('cart')) {
      cart.push(this.product);
    } else {
      cart = JSON.parse(localStorage.getItem('cart'));
      for (let i = 0; i < cart.length; i++) {
        let currentItem = cart[i];
        if (currentItem._id === this.product._id) {
          duplicateProduct = true;
          break;
        }
      }
      if (!duplicateProduct) {
        cart.push(this.product);
      }
    }
    if (!duplicateProduct) {
      localStorage.setItem('cart', JSON.stringify(cart));
      this.toastr.success('Added product to cart!', 'Success!');
      this.router.navigateByUrl('/rims/catalog');
    } else {
      this.toastr.error('You already have that product in your cart!', 'Error!');
    }
  }
}
