import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AdminGuard } from "../authentication/guards/admin.guard";

//Components
import { AllRimsComponent } from "./all-rims/all-rims.component";
import { CreateRimComponent } from "./create-rim/create-rim.component";
import { EditRimComponent } from "./edit-rim/edit-rim.component";
import { DetailsRimComponent } from "./details-rim/details-rim.component";

const rimRoutes: Routes = [
  { path: 'catalog', component: AllRimsComponent },
  { path: 'details/:id', component: DetailsRimComponent },
  { path: 'create', component: CreateRimComponent, canActivate: [ AdminGuard ] },
  { path: 'edit/:id', component: EditRimComponent, canActivate: [ AdminGuard ] },
];

@NgModule({
  imports: [
    RouterModule.forChild(rimRoutes)
  ],
  exports: [
    RouterModule
  ]
})

export class RimsRoutingModule { }
