import {
  HttpResponse,
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest, HttpErrorResponse
} from "@angular/common/http";

import { tap } from "rxjs/internal/operators";

import { Observable } from "rxjs/index";
import { Injectable } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";

//Constants
const kinveyAppKey = "kid_SJEtf6PHm";
const kinveyAppSecret = "18804c92e53e442593df679e37044f6d";

@Injectable()
export class KinveyInterceptor implements HttpInterceptor {
  constructor(private toastr: ToastrService, private router: Router) {

  }

  intercept(request: HttpRequest<any>, next: HttpHandler)
  : Observable<HttpEvent<any>> {
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.authtoken) {
      request = request.clone({
        setHeaders: {
          'Authorization': 'Kinvey ' + user.authtoken
        }
      })
    } else {
      request = request.clone({
        setHeaders: {
          'Authorization': 'Basic ' + btoa(kinveyAppKey + ':' + kinveyAppSecret)
        }
      })
    }

    return next.handle(request)
      .pipe(tap((res : any) => {

        //Login
        if (res instanceof HttpResponse && res.body._kmd && res.body._kmd.authtoken && res.url.endsWith('/login')) {
          this.saveToken(res.body);
          this.toastr.success('Logged in!', 'Success!');
          this.router.navigateByUrl('/account');
        }

        //Register
        if (res instanceof HttpResponse && res.url.endsWith(kinveyAppKey)) {
          this.toastr.success('Registered!, Please now login.', 'Success!');
          this.router.navigateByUrl('/login');
        }

        //Create rim ad
        if (res instanceof HttpResponse && res.statusText === 'Created' && res.url.endsWith('rims')) {
          this.toastr.success('Created rim ad!', 'Success!');
          this.router.navigateByUrl('/rims/catalog');
        }

    }));
  }

  private saveToken(data) {
    let user = data;
    localStorage.setItem('user', JSON.stringify({
      'username': user.username,
      'authtoken': user._kmd.authtoken,
      'isAdmin': user.isAdmin,
      'userId': user._id
    }))
  }
}
