import { Component, OnInit } from '@angular/core';
import { AuthService } from "../auth.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css']
})
export class AccountComponent implements OnInit {
  accountModel : any;
  constructor(private authService: AuthService,
              private toastr: ToastrService) { }

  ngOnInit() {
    let userId = JSON.parse(localStorage.getItem('user')).userId;
    this.authService.getUserById(userId)
      .subscribe((data) => {
        this.accountModel = data;
      }, (err) => {
        this.toastr.error('Could not load your data!', 'Error!');
      })
  }

  submitChanges() {
    this.authService.updateUserById(JSON.parse(localStorage.getItem('user')).userId, this.accountModel)
      .subscribe((data) => {
        this.toastr.success('Account changes saved!', 'Success!');
      }, (err) => {
        this.toastr.error('Something went wrong!', 'Error!');
      });
  }
}
