import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

//Models.
import { CreateRimModel } from "./models/create-rim-model";
import { RimModel } from "./models/rim-model";

//Constants
const kinveyBaseUrl = "https://baas.kinvey.com/";
const kinveyAppKey = "kid_SJEtf6PHm";

//Requests URLs.
const allRimsUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/rims';
const getRimByIdUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/rims/';
const createRimUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/rims';
const editRimUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/rims/';
const deleteRimUrl = kinveyBaseUrl + 'appdata/' + kinveyAppKey + '/rims/';

@Injectable()
export class RimsService {
  constructor(private http: HttpClient) { }
  //Create
  createRim(body: CreateRimModel) {
    return this.http.post(createRimUrl, body);
  }

  //Retrieve
  getAllRims() {
    return this.http.get<RimModel[]>(allRimsUrl);
  }

  getRimById(id: string) {
    return this.http.get<RimModel>(getRimByIdUrl + id);
  }

  //Update
  editRim(id: string, body: RimModel) {
    return this.http.put(editRimUrl + id, body);
  }

  //Delete
  deleteRim(id: string) {
    return this.http.delete(deleteRimUrl + id);
  }
}
