import { Component, OnInit } from '@angular/core';
import { Observable } from "rxjs/index";
import { RimModel } from "../models/rim-model";

import { RimsService } from "../rims.service";
import { ToastrService } from "ngx-toastr";
import { AuthService } from "../../authentication/auth.service";

@Component({
  selector: 'app-all-rims',
  templateUrl: './all-rims.component.html',
  styleUrls: ['./all-rims.component.css']
})
export class AllRimsComponent implements OnInit {
  rims: Observable<RimModel[]>;
  currentPage: number = 1;
  pageSize: number = 6;

  constructor(private rimsService: RimsService,
              private toastr: ToastrService,
              private authService: AuthService)
  { }

  ngOnInit() {
    this.rims = this.rimsService.getAllRims();
  }

  changePage(page) {
    this.currentPage = page;
  }

  deleteRimAd(id: string) {
    this.rimsService.deleteRim(id)
      .subscribe(() => {
        this.toastr.success('Rim deleted successfully!', 'Success!');
        this.rims = this.rimsService.getAllRims();
      });
  }

}
