import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SignUpModel } from './models/signup.model';
import { SignInModel } from './models/signin.model';
import { AccountModel } from "./models/account.model";

const kinveyBaseUrl = "https://baas.kinvey.com/";
const kinveyAppKey = "kid_SJEtf6PHm";

const loginUrl = kinveyBaseUrl + 'user/' + kinveyAppKey + '/login';
const registerUrl = kinveyBaseUrl + 'user/' + kinveyAppKey;
const userByIdUrl = kinveyBaseUrl + 'user/' + kinveyAppKey + '/';

@Injectable()
export class AuthService {
  username: string;
  constructor(private http : HttpClient) {  }

  register(body : SignUpModel) {
    return this.http.post(registerUrl, body);
  }

  login(body : SignInModel) {
    return this.http.post(loginUrl, body);
  }

  logout() {
    localStorage.clear();
  }

  getUserById(id: string) {
    return this.http.get(userByIdUrl + id);
  }

  updateUserById(id: string, body: AccountModel) {
    return this.http.put(userByIdUrl + id, body);
  }

  isAuthenticated() : boolean {
    return localStorage.getItem('user') !== null;
  }

  isAdmin() : boolean {
    if (this.user) {
      return this.user.isAdmin;
    }
    return false;
  }

  get user() {
    let currentUser = JSON.parse(localStorage.getItem('user'));
    return currentUser;
  }
}
