import { Component, OnInit } from '@angular/core';
import { CreateRimModel } from "../models/create-rim-model";
import { RimsService } from "../rims.service";

@Component({
  selector: 'app-create-rim',
  templateUrl: './create-rim.component.html',
  styleUrls: ['./create-rim.component.css']
})
export class CreateRimComponent implements OnInit {
  createModel: CreateRimModel;

  constructor(private rimsService: RimsService) {
    this.createModel = new CreateRimModel('', '', '', '', '', '', '', '', 0, '');
  }

  ngOnInit() {
  }

  create() {
    this.rimsService.createRim(this.createModel)
      .subscribe();
  }
}
